@import 'styles';

.background {
  width: 100%;
  min-height: 100vh;
  background-size: cover;
  background-color: $white;
  background-image: url('./static/background.jpg');

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 10rem 1rem;

    @media ($desktop) {
      padding: 10rem 0;
    }

    .title {
      color: $white;
      margin-bottom: 5rem;
      text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.8);
      font-size: 3rem;
    }

    .filters {
      border-radius: 0.25rem;

      @media ($desktop) {
        width: 500px;
        margin-top: 10rem;
      }
    }
  }
}

.session {
  margin-bottom: 2.5rem;

  .subtitle {
    font-size: 2rem;
    margin: 2.5rem 0;
    text-align: center;
    font-weight: bold;
    color: $black;
  }

  .products {
    display: grid;
    grid-template-rows: repeat(3, minmax(0, 1fr));
    grid-template-columns: 1fr;

    @media ($desktop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      grid-template-rows: 1fr;
    }
  }
}
